var { log } = console;
// 引入提示组件
import HMmessages from '@/components/HM-messages/HM-messages.vue';
// post
import { API } from '../../api/request.js';
const api = new API();
export default {
	components: { HMmessages },
	data() {
		return {
			// 是否禁掉按钮点击
			btnlist: true,
			Comment: '',
			place: '',
			ide: '',
			userdata: {}
		};
	},

	methods: {
		bTn() {
			log(this.Comment);
			if (this.Comment == '') {
				let succ = '评论不能为空';
				let ico = 'error';
				this.tIps(succ, ico);
				return;
			}
			log('可以评论');
			// 这里是还没请求完成进行禁止点击
			this.btnlist = false;
			this.messAge();
		},

		// 请求-评论
		messAge() {
			let data = {
				messages: this.Comment,
				avatarUrl: this.userdata.avatarUrl,
				nickName: this.userdata.nickName,
				openid: this.userdata.openid,
				merchantid: this.ide
			};
			uni.showToast({
				icon:'loading',
				mask:true,
				duration:900000,
				position:'center',
				title:'正在发表评论'
			})
			api.postcommenturl(data)
				.then(res => {
					log(res);
					this.Comment = '';
					let succ = '评论成功';
					let ico = 'success';
					this.tIps(succ, ico);
					this.btnlist = true;
					uni.hideToast();
				})
				.catch(err => {
					uni.hideToast();
					log(err);
				});
		},

		// 提示
		tIps(succ, ico) {
			this.HMmessages.show(succ, { icon: ico, fontColor: '#ffffff', iconColor: '#ffffff', background: 'rgba(102,0,51,0.8)' });
		}
	},

	onLoad(e) {
		log(e);
		// 商家名称
		this.place = '写下你对商家' + e.body + '的评价';
		// 商家标识
		this.ide = e.ide;
		// 用户信息
		this.userdata = uni.getStorageSync('username');
	}
};
